<template>
  <div id="app">
    <!-- Alert Tidak Punya Akses Halaman -->
    <v-col v-show="this.valid == false">
      <v-alert text type="error">
        Kamu Tidak punya Akses Ke halaman "Karyawan"
      </v-alert>
    </v-col>

    <!-- Alert Data Tersimpan -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dark
      dismissible
      elevation="4"
      color="success"
      v-model="Alert"
      class="rounded-lg"
      icon="mdi-check-bold"
      transition="slide-x-transition"
    >
      <v-icon class="mr-1">check-circle</v-icon>
      Data Berhasil Disimpan
    </v-alert>

    <!-- Alert Data Berhasil Terubah -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dark
      dismissible
      elevation="4"
      color="success"
      class="rounded-lg"
      icon="mdi-check-bold"
      v-model="AlertBerhasilTerubah"
      transition="slide-x-transition"
    >
      Data Berhasil Diubah
    </v-alert>

    <!-- Alert Data Sudah Terhapus -->
    <v-alert style="position:fixed;z-index:2;right:20px;"
      dark
      dismissible
      color="error"
      elevation="4"
      class="rounded-lg"
      icon="mdi-delete"
      v-model="AlertDataTerhapus"
      transition="slide-x-transition"
    >
      Data Berhasil Dihapus
    </v-alert>

    <div v-show="loading == true">
      <v-progress-linear
        :active="loading"
        :indeterminate="loading"
        absolute
        top
        height="6"
        color="red darken-4"
      ></v-progress-linear>
    </div>

    <v-container fluid v-show="this.valid == true">
      <v-toolbar
        flat
        height="55"
        :color="$vuetify.theme.dark ? '' : 'grey lighten-2'"
        class="mb-3 rounded-lg"
        v-resize="onResize"
      >
        <v-toolbar-title>Data Karyawan</v-toolbar-title>

        <v-spacer></v-spacer>

        <!-- refresh mode biasa  -->
        <!-- <v-btn
          @click="refreshdata"
          v-show="mobile == false"
          class="subtitle-1 text-capitalize ma-2"
          outlined
          text
          :disabled="loading == true"
        >
        <v-icon class="mr-1">mdi-refresh</v-icon>
          <span>Refresh Data</span>
        </v-btn> -->
        <!-- refresh mode mobile -->

        <v-btn
          v-show="mobile == true"
          @click="refreshdata"
          class="subtitle-1 text-capitalize ma-2"
          outlined
          small
          text
          fab
          :disabled="loading == true"
        >
          <v-icon class="ml-1">mdi-refresh</v-icon>
        </v-btn>

        <!-- Menu Export Data -->
        <div class="text-center mx-2">
          <v-menu
            v-model="MenuExport"
            :close-on-content-click="false"
            :nudge-width="1"
            rounded="lg"
            offset-y
          >
            <template v-slot:activator="{ on, attrs }">
              <!-- Btn Export Mode Biasa -->
              <v-btn
                @click="MenuExport = !MenuExport"
                class="text-capitalize rounded-lg"
                v-show="mobile == false"
                v-bind="attrs"
                v-on="on"
                outlined
                text
                :disabled="loading == true"
              >
                <v-icon left>mdi-table-arrow-down</v-icon>
                <span class="subtitle-1 mr-n1">Export Data</span>
                <v-icon right>{{ MenuExport ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
              </v-btn>
            </template>

            <v-card class="pa-2" v-show="MenuExport" :outlined="$vuetify.theme.dark ? true : false">   
              <!-- PDF -->
              <v-btn
                block
                depressed
                @click.native="PdfExport"
                class="text-capitalize subtitle-1 rounded-lg mb-2"
              >
                Pdf
              </v-btn>
            
              <!-- EXCEL -->
              <v-btn
                block
                depressed                       
                @click.native="excelExport"
                class="text-capitalize subtitle-1 rounded-lg"
              >
                Excel
              </v-btn>
            </v-card>
          </v-menu>

          <!-- Btn Export Dialog Mode Mobile -->
          <v-dialog
            v-model="DialogPilihExport"
            max-width="200"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-show="mobile == true"
                v-bind="attrs"
                v-on="on"
                outlined
                x-small text
                class="rounded-lg"
                height="40px" width="40px"
                :disabled="loading == true"
              >
                <v-icon>mdi-table-arrow-down</v-icon>
              </v-btn>
            </template>
            <v-card class="rounded-lg">
              <v-toolbar dense>
                <v-layout justify-center>
                  <v-toolbar-title>
                    Pilih Export File
                  </v-toolbar-title>
                </v-layout>
              </v-toolbar>
              <v-card class="pa-2">
                <v-btn text block outlined class="text-capitalize subtitle-1 mb-2" @click.native="PdfExport">
                  Pdf
                  <v-icon right>mdi-adobe-acrobat</v-icon>
                </v-btn>

                <v-btn text block outlined class="text-capitalize subtitle-1" @click.native="excelExport">
                  Excel
                  <v-icon right>mdi-microsoft-excel</v-icon>
                </v-btn>
              </v-card>
            </v-card>
          </v-dialog>
        </div>

        <!-- Dialog Tambah Karyawan -->
        <v-dialog
          v-model="DialogTambahKaryawan"
          max-width="700px"
          scrollable
          persistent
        >
          <template v-slot:activator="{ on, attrs }">
            <!-- Btn Tambah Mode Desktop -->
            <v-btn
              class="text-capitalize mr-n2 rounded-lg"
              v-show="mobile == false && ValidCreate == true"
              v-bind="attrs"
              v-on="on"
              color="success"
              depressed
              :disabled="loading == true"
            >
              <v-icon left>mdi-plus-thick</v-icon>
              <span class="subtitle-1">Tambah</span>
            </v-btn>

            <!-- Btn Tambah Mode Desktop Kondisi Tidak bisa tambah user -->
            <v-btn
              class="text-capitalize mr-n2 rounded-lg"
              v-show="mobile == false && ValidCreate == false"
              v-bind="attrs"
              v-on="on"
              color="success"
              depressed
              :disabled="ValidCreate == false"
            >
              <v-icon left>mdi-plus-thick</v-icon>
              <span class="subtitle-1">Tambah</span>
            </v-btn>

            <!-- Btn Tambah Mode Mobile -->
            <v-btn 
              class="mr-n2 rounded-lg"
              v-show="mobile == true && ValidCreate == true"
              v-bind="attrs"
              v-on="on"
              x-small
              depressed
              color="success"
              height="40px" width="40px"
              :disabled="loading == true"
            >
              <v-icon>mdi-plus-thick</v-icon>
            </v-btn>
            
            <!-- Btn Tambah Mode Mobile Kondisi Tidak bisa tambah user -->
            <v-btn 
              class="mr-n2 rounded-lg"
              v-show="mobile == true && ValidCreate == false"
              x-small
              depressed
              color="success"
              height="40px" width="40px"
              :disabled="ValidCreate == false"
            >
              <v-icon>mdi-plus-thick</v-icon>
            </v-btn>
          </template>
          <v-card outlined class="rounded-lg">
            <v-toolbar flat height="64">
              <v-toolbar-title>
                <div class="text-truncate"><span>{{ formTitleKaryawan }}</span></div>
                <div class="mt-n1 caption text-truncate"><span v-text="TampilNama"></span></div>
              </v-toolbar-title>
              <v-spacer></v-spacer>
              <!-- Button Menu Tampilan Buka Semua Inputan Atau Tutup -->
              <v-menu :close-on-content-click="closeOnContentClick">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    icon
                    v-bind="attrs"
                    v-on="on"
                  >
                    <v-icon>mdi-dots-vertical</v-icon>
                  </v-btn>
                </template>
                <v-card class="rounded-lg pa-2">
                  <v-list class="mt-n2 mb-n2">
                    <v-btn text block small outlined rounded class="text-capitalize" @click="ShowAll">Tampilkan Semua</v-btn>
                  </v-list>
                  <v-list class="mb-n2">
                    <v-btn text block small outlined rounded class="text-capitalize" @click="CloseAll">Tutup Semua</v-btn>
                  </v-list>                      
                </v-card>
              </v-menu>

              <v-btn icon @click="KeluarDialogKaryawan">
                <v-icon>mdi-window-close</v-icon>
              </v-btn>
            </v-toolbar>

            <!-- <v-sheet class="pr-2 pl-3">
              <v-layout justify-center>
                <v-chip-group :column="mobile ? false : true" mandatory :active-class="$vuetify.theme.dark ? 'grey darken-3 white--text' : 'grey darken-3 white--text'">
                  <v-chip @click="input_data_karyawan" filter-icon="mdi-account-details" :class="mobile ? 'subtitle-1' : ''" :color="$vuetify.theme.dark ? 'grey darken-4' : 'grey lighten-3'"><v-icon left>mdi-account-details</v-icon>Data</v-chip>
                  <v-chip @click="input_foto" filter-icon="mdi-upload" :class="mobile ? 'subtitle-1' : ''" :color="$vuetify.theme.dark ? 'grey darken-4' : 'grey lighten-3'"><v-icon left>mdi-upload</v-icon>Foto</v-chip>
                  <v-chip @click="input_kontrak" filter-icon="mdi-file-document-edit" :class="mobile ? 'subtitle-1' : ''" :color="$vuetify.theme.dark ? 'grey darken-4' : 'grey lighten-3'"><v-icon left>mdi-file-document-edit</v-icon>Kontrak</v-chip>
                  <v-chip @click="input_jabatan" filter-icon="mdi-account-hard-hat" :class="mobile ? 'subtitle-1' : ''" :color="$vuetify.theme.dark ? 'grey darken-4' : 'grey lighten-3'"><v-icon left>mdi-account-hard-hat</v-icon>Jabatan</v-chip>
                  <v-chip @click="input_user" filter-icon="mdi-account-circle" :class="mobile ? 'subtitle-1' : ''" :color="$vuetify.theme.dark ? 'grey darken-4' : 'grey lighten-3'"><v-icon left>mdi-account-circle</v-icon>User</v-chip>
                  <v-chip @click="input_vaksin" filter-icon="mdi-needle" :class="mobile ? 'subtitle-1' : ''" :color="$vuetify.theme.dark ? 'grey darken-4' : 'grey lighten-3'"><v-icon left>mdi-needle</v-icon>Vaksin</v-chip>
                </v-chip-group>
              </v-layout>
            </v-sheet> -->
            
            <v-divider></v-divider>
            
            <v-card-text :class="$vuetify.theme.dark ? 'black' : 'grey lighten-3'">
              <div class="mr-n3 ml-n3 mb-n2 mt-3">
                <!-- <v-expansion-panels multiple focusable>
                  <v-expansion-panel value=true>
                    <v-expansion-panel-header expand-icon="mdi-menu-down"><strong><v-icon left>mdi-account-details</v-icon>Data</strong></v-expansion-panel-header>
                    <v-expansion-panel-content>
                      aaaaaaaaa
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                  <v-expansion-panel>
                    <v-expansion-panel-header expand-icon="mdi-menu-down"><strong><v-icon left>mdi-upload</v-icon>Foto</strong></v-expansion-panel-header>
                    <v-expansion-panel-content>
                      aaaa
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                  <v-expansion-panel>
                    <v-expansion-panel-header expand-icon="mdi-menu-down"><strong><v-icon left>mdi-file-document-edit</v-icon>Kontrak</strong></v-expansion-panel-header>
                    <v-expansion-panel-content>
                      sdsda
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                  <v-expansion-panel>
                    <v-expansion-panel-header expand-icon="mdi-menu-down"><strong><v-icon left>mdi-account-hard-hat</v-icon>Jabatan</strong></v-expansion-panel-header>
                    <v-expansion-panel-content>
                      dfsfqwdqwfd
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                  <v-expansion-panel>
                    <v-expansion-panel-header expand-icon="mdi-menu-down"><strong><v-icon left>mdi-account-circle</v-icon>User</strong></v-expansion-panel-header>
                    <v-expansion-panel-content>
                      fveqd
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                  <v-expansion-panel>
                    <v-expansion-panel-header expand-icon="mdi-menu-down"><strong><v-icon left>mdi-needle</v-icon>Vaksin</strong></v-expansion-panel-header>
                    <v-expansion-panel-content>
                      fveqd
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                </v-expansion-panels> -->


                <v-sheet outlined class="pa-2 mx-auto" max-width="700">
                  <!-- Data -->
                  <div>
                    <v-sheet outlined>
                      <a>
                        <v-hover
                          v-slot="{ hover }"
                        >
                          <v-sheet
                            @click="showData = !showData"
                            :dark="hover ? true : false"
                            :color="hover ? 'grey darken-2' : '$vuetify.theme.dark'"
                            :class="hover == { 'on-hover': hover } == $vuetify.theme.dark ? 'grey lighten-4' : '$vuetify.theme.dark'"
                          >
                            <v-card-actions>
                              <v-icon left>mdi-account-details</v-icon>
                              <span class="subtitle-1 font-weight-medium">Data</span>
                              <v-spacer></v-spacer>
                              <v-btn text small outlined class="text-capitalize">{{ showData ? 'Tutup' : 'Tampilkan' }}</v-btn>
                            </v-card-actions>
                          </v-sheet>
                        </v-hover>
                      </a>

                      <v-expand-transition transition="false">
                        <div v-show="showData">
                          <v-divider></v-divider>
                          <div class="pa-6">
                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Nama<strong class="error--text">*</strong></span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-text-field
                                  dense
                                  color="dark"
                                  v-model="editedItem.Nama"
                                  hint="Wajib Diisi." 
                                  :rules="[value => !!value || 'Tidak Boleh Kosong']"
                                >
                                  <template v-slot:prepend v-if="mobile == false">
                                    <span>:</span>
                                  </template>
                                </v-text-field>
                              </v-col>
                            </v-row>

                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">NRK<span class="error--text">*</span></span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-text-field
                                  dense
                                  :disabled="editedIndex == -1 ? true : false "
                                  color="dark"
                                  v-model="editedItem.NRK"
                                >
                                  <template v-slot:prepend v-if="mobile == false">
                                    <span>:</span>
                                  </template>
                                </v-text-field>
                              </v-col>
                            </v-row>

                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">POH</span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-text-field
                                  dense
                                  color="dark"
                                  v-model="editedItem.POH"
                                  hint="Wajib Diisi." 
                                  :rules="[value => !!value || 'Tidak Boleh Kosong']"
                                >
                                  <template v-slot:prepend v-if="mobile == false">
                                    <span>:</span>
                                  </template>
                                </v-text-field>
                              </v-col>
                            </v-row>

                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Lokasi</span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-text-field
                                  dense
                                  color="dark"
                                  v-model="editedItem.Lokasi"
                                >
                                  <template v-slot:prepend v-if="mobile == false">
                                    <span>:</span>
                                  </template>
                                </v-text-field>
                              </v-col>
                            </v-row>

                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Cabang<span class="error--text">*</span></span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-combobox
                                  v-model="editedItem.Cabang"
                                  :items="DataCabang"
                                  color="dark"
                                  multiple
                                  item-text="NamaCabang"
                                  dense
                                >
                                  <template v-slot:prepend v-if="mobile == false">
                                    <span>:</span>
                                  </template>
                                </v-combobox>
                              </v-col>
                            </v-row>

                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Kategori Absen<span class="error--text">*</span></span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-autocomplete
                                  dense
                                  color="dark"
                                  :items="DataKategoriAbsen"
                                  item-text="Kategori"
                                  item-value="id"
                                  v-model="editedItem.KategoriAbsen"
                                >
                                  <template v-slot:prepend v-if="mobile == false">
                                    <span>:</span>
                                  </template>
                                </v-autocomplete>
                              </v-col>
                            </v-row>

                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Tanggal Masuk</span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-dialog
                                  v-model="MenuTglMasuk"
                                  :return-value.sync="date"
                                  width="290px"
                                >
                                  <template v-slot:activator="{ on, attrs }">
                                    <v-text-field
                                      v-model="editedItem.TglMasuk"
                                      append-icon="mdi-calendar"
                                      color="dark"
                                      readonly
                                      dense
                                      v-bind="attrs"
                                      v-on="on"
                                    >
                                      <template v-slot:prepend v-if="mobile == false">
                                        <span>:</span>
                                      </template>
                                    </v-text-field>
                                  </template>
                                  <v-date-picker
                                    v-model="editedItem.TglMasuk"
                                    @input="MenuTglMasuk = false"
                                    year-icon="mdi-calendar-blank"
                                    color="red darken-4"
                                    class="rounded-lg"
                                    locale="id"
                                    scrollable
                                  ></v-date-picker>
                                </v-dialog>
                              </v-col>
                            </v-row>

                            <v-row no-gutters v-show="formTitleKaryawan == 'Ubah Data Karyawan'">
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Masa Kerja</span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-text-field
                                  dense
                                  readonly
                                  color="dark"
                                  v-model="MasaKerja"
                                >
                                  <template v-slot:prepend v-if="mobile == false">
                                    <span>:</span>
                                  </template>
                                </v-text-field>
                              </v-col>
                            </v-row>

                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Sisa Cuti</span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-text-field
                                  dense
                                  color="dark"
                                  v-model="editedItem.Cuti"
                                >
                                  <template v-slot:prepend v-if="mobile == false">
                                    <span>:</span>
                                  </template>
                                </v-text-field>
                              </v-col>
                            </v-row>

                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Tempat Lahir</span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-text-field
                                  dense
                                  color="dark"
                                  v-model="editedItem.TempatLahir"
                                >
                                  <template v-slot:prepend v-if="mobile == false">
                                    <span>:</span>
                                  </template>
                                </v-text-field>
                              </v-col>
                            </v-row>

                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Tanggal Lahir</span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-dialog
                                  v-model="MenuTglLahir"
                                  :return-value.sync="date"
                                  width="290px"
                                >
                                  <template v-slot:activator="{ on, attrs }">
                                    <v-text-field
                                      v-model="editedItem.TglLahir"
                                      append-icon="mdi-calendar"
                                      color="dark"
                                      readonly
                                      dense
                                      v-bind="attrs"
                                      v-on="on"
                                    >
                                      <template v-slot:prepend v-if="mobile == false">
                                        <span>:</span>
                                      </template>
                                    </v-text-field>
                                  </template>
                                  <v-date-picker
                                    v-model="editedItem.TglLahir"
                                    @input="MenuTglLahir = false"
                                    year-icon="mdi-calendar-blank"
                                    color="red darken-4"
                                    class="rounded-lg"
                                    :max="new Date().toISOString().substr(0, 10)"
                                    min="1950-01-01"
                                    locale="id"
                                    ref="picker"
                                  ></v-date-picker>
                                </v-dialog>
                              </v-col>
                            </v-row>

                            <v-row no-gutters>
                              <v-col cols="12" sm="4" md="4" lg="4">
                                <span :class="mobile ? 'subtitle-2' : 'subtitle-1'">Alamat KTP</span>
                              </v-col>
                              <v-col cols="12" sm="8" md="8" lg="8" class="mt-n1">
                                <v-textarea
                                  no-resize
                                  dense
                                  rows="2"
                                  color="dark"
                                  v-model="editedItem.AlamatKTP"
                                >
                                  <template v-slot:prepend v-if="mobile == false">
                                    <span>:</span>
                                  </template>
                                </v-textarea>
                              </v-col>
                            </v-row>

