                              </v-chip-group>
                            </v-layout>
                          </v-sheet>

                          <v-divider></v-divider>

                          <v-tabs-items v-show="ShowTabFotoKaryawan">
                            <v-scroll-y-transition>
                              <v-layout justify-center v-if="ShowTabFotoKaryawan">
