    <!-- Dialog Lihat Detail Per Karyawan -->
    <v-dialog   
      scrollable
      max-width="1000"
      v-model="DialogLihatDataKaryaran"
      :fullscreen="mobile ? true : false"
      :transition="mobile ? 'dialog-bottom-transition' : 'scale-transition'"
    >
      <v-card outlined class="rounded-lg">
        <v-toolbar flat height="64">
          <v-btn
            icon
            v-show="mobile == true"
            @click="KeluarLihatDataKaryawan"
          >
            <v-icon>mdi-arrow-left</v-icon>
          </v-btn>
          <v-toolbar-title>                
            <span class="ml-n5">Detail Karyawan</span>
          </v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn
            icon
            v-show="mobile == false"
            @click="KeluarLihatDataKaryawan"
          >
            <v-icon>mdi-window-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-divider></v-divider>

        <!-- Mode Desktop Start -->
        <v-card-text
          v-show="mobile == false"
          :class="$vuetify.theme.dark ? 'black' : 'grey lighten-4'"
        > 
          <div class="mr-n3 ml-n3 mb-n2 mt-3">
            <v-row>
              <v-col cols="2">
                <v-card @click="DialogLihatFoto = true" style="cursor: pointer; width: 15%; position: absolute;" outlined class="pa-1 rounded-lg elevation-3">
                  <v-card
                    outlined
                    :color="$vuetify.theme.dark ? 'grey darken-3' : 'grey lighten-2'"
                    class="fill-height rounded-lg"
                  >
                    <v-responsive :aspect-ratio="3/4.3">
                      <v-img
                        v-if="foto"
                        :src="foto"
                        class="d-flex flex-row fill-height rounded-lg"
                      >
                        <template v-slot:placeholder>
                          <v-row
                            class="fill-height ma-0"
                            align="center"
                            justify="center"
                          >
                            <span :class="$vuetify.theme.dark ? 'grey--text' : 'grey--text'">no image</span>
                          </v-row>
                        </template>
                      </v-img>
                    </v-responsive>  
                  </v-card>
                </v-card>
              </v-col>

              <v-col cols="10">
                <v-sheet
                  outlined
                  class="rounded-lg mb-n2"
                >
                  <div class="pa-2 ml-1 subtitle-1">
                    <v-row no-gutters>
                      <label style="width: 33%;">Nama</label>
                      <label style="width: 2%;">:</label>
                      <label style="width: 65%;" v-show="loading_show_data_karyawan == false">{{ editedItem.Nama }}</label>
                    </v-row>
                  </div>

                  <v-divider></v-divider>

                  <div class="pa-2 ml-1 subtitle-1">
                    <v-row no-gutters>
                      <label style="width: 33%;">NRK</label>
                      <label style="width: 2%;">:</label>
                      <label style="width: 65%;" v-show="loading_show_data_karyawan == false">{{ editedItem.NRK }}</label>
                    </v-row>
