    dataStateChange: function(state) { 
      this.statechange = state
        if (state.action.requestType === 'paging') {
          this.skip = state.skip
          this.take = state.take
          if (state.search != undefined) {
            api.get('karyawan?token='+this.token+'&search='+state.search[0].key+'&skip='+state.skip+'&take='+state.take,{
                transformResponse: api.defaults.transformResponse.concat((data) => {
                  this.loading = false
                  data.result = this.$decrypt(JSON.parse(data.result))
                  this.DataKaryawan = data
                })
              },
              err => {
                console.log(err);
              }
            ) 
            // ).then(
            //   res => {
            //     this.DataKaryawan = res.data
            //   }
            // )
          } else if (state.search == undefined) {
            this.getDataPage(state.skip,state.take,this.sorted != null ? this.sorted : undefined,this.filter != null ? this.filter : undefined)
          }
        }
        if (state.action.requestType === 'sorting' || state.action.requestType === "grouping" || state.action.requestType === "ungrouping") {
          // SORTIR GROUPING UNGROUPING
          // MULTI GROUPING
          this.$refs.gridkaryawan.clearFiltering()
          this.sorted = state.sorted == undefined ? undefined : state.sorted.length > 1 ? state.sorted.map(element => {
            return element.name+ ' ' + element.direction
          }) : state.sorted[0].name+' '+state.sorted[0].direction
          if (state.sorted != undefined && state.sorted.length > 1) {
            this.sorted = new URLSearchParams({st:this.sorted}).toString()
          }
          this.getDataPage(state.skip,state.take,this.sorted)
          this.query = null
          this.filter = null
        }
        if (state.action.requestType === 'filterchoicerequest') {
          var objDistinct = {}
          let queryString = state.action.query.distincts.length > 1 ? state.action.query.distincts.at(-1) + ';' + new URLSearchParams(objDistinct).toString() : state.action.query.distincts[0]
          if (this.query != null) {
            queryString = queryString+';'+this.query
          }
          api.get('karyawan?token='+this.token+'&dist='+queryString,{
                transformResponse: api.defaults.transformResponse.concat((data) => {
                  let res = this.$decrypt(JSON.parse(data.result))
                  state.dataSource(res)
                  localStorage.setItem('ds',JSON.stringify(res))
                })
              },
              err => {
                console.log(err);
              }
            ) 
          // ).then(
          //   res => {
          //   state.dataSource(res.data.result)
          //   localStorage.setItem('ds',JSON.stringify(res.data.result))
          //   }
          // );
        }
        if (state.action.requestType === "filtersearchbegin") {
          if (state.action.value == undefined) {
            state.dataSource(JSON.parse(localStorage.getItem('ds')))
          } else {
            state.dataSource(this.filterByValue(JSON.parse(localStorage.getItem('ds')),state.action.value))
          }
        }
        if (state.action.requestType === "filtering" && state.action.action !== "clear-filter") {
          let filter = {}
          let val = []
          let prop = state.where[0].predicates[0].predicates == undefined ? state.where[0].predicates[0].field : state.where[0].predicates[0].predicates[0].field
          if (state.where[0].predicates[0].predicates == undefined) {
            val.push(state.where[0].predicates[0].value)
          } else {
            state.where[0].predicates[0].predicates.forEach(element => {
              val.push(element.value)
            });
          }
          filter[prop] = val
          let url = new URLSearchParams(filter).toString()
          this.query = this.query != null ? this.query + ';' + url : url
          if (this.filter != null && this.filter.includes(prop)) {
            let jadiArr = this.filter.split(';')
            jadiArr.forEach(function(element,index,array) {
              if (element.includes(prop)) {
                array[index] = url
              }
            });
            this.filter = jadiArr.toString().replaceAll(',',';')
          } else {
            this.filter = this.filter != null ? this.filter + ';' + url : url
          }
          this.getDataPage(state.skip,state.take,undefined,this.filter)
          this.$refs.gridkaryawan.clearFiltering();
        }
        if (state.action.action === "clear-filter") {
          this.getDataPage(state.skip,state.take,this.sorted != null ? this.sorted : undefined)
          this.query = null
          this.filter = null
        }
        if (state.action.requestType === "searching") {
          state.skip = 0
          state.take = 10
          if (state.action.searchString == "" && this.$refs.gridkaryawan.$el.ej2_instances[0].pageSettings.currentPage == 1) {
            this.getDataPage(0,10)
          } else if (this.$refs.gridkaryawan.$el.ej2_instances[0].pageSettings.currentPage == 1) {
            api.get('karyawan?token='+this.token+'&search='+state.action.searchString+'&skip='+0+'&take='+100,{
                transformResponse: api.defaults.transformResponse.concat((data) => {
                  data.result = this.$decrypt(JSON.parse(data.result))
                  this.DataKaryawan = data
                })
              },
              err => {
                console.log(err);
              }
            ) 
            // ).then(
            //   res => {
            //     this.DataKaryawan = res.data
            //   }
            // )
          } else {
            this.$refs.gridkaryawan.$el.ej2_instances[0].pageSettings.currentPage = 1
          }
        }
      },
